<template>
  <div class="main">
    <el-page-header @back="goBack" :content="data.name"></el-page-header>
    <el-tabs v-model="activeName">
      <el-tab-pane label="管理员" name="first">
        <div class="select">
          <el-input v-model="gid" placeholder="输入管理员账号" clearable></el-input>
          <el-button type="primary" @click="submit">添加</el-button>
        </div>
        <el-table
          border
          tooltip-effect="dark"
          style="width: 100%"
          :header-cell-style="{background:'#f5f7fa'}"
          :default-sort="{prop: 'gid'}"
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        >
          <el-table-column prop="gid" label="账号" sortable></el-table-column>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column align="right">
            <template slot="header">
              <el-input v-model="search" size="mini" placeholder="输入姓名搜索" />
            </template>
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">移除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="权限" name="second">
        <el-form ref="form" :model="form" label-width="90px">
          <el-divider content-position="left">页面权限</el-divider>
          <el-form-item label="人员管理">
            <el-checkbox-group v-model="form.person">
              <el-checkbox label="增" name="person"></el-checkbox>
              <el-checkbox label="删" name="person"></el-checkbox>
              <el-checkbox label="改" name="person"></el-checkbox>
              <el-checkbox label="查" name="person"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="日志管理">
            <el-checkbox-group v-model="form.log">
              <el-checkbox label="增" name="log" disabled></el-checkbox>
              <el-checkbox label="删" name="log" disabled></el-checkbox>
              <el-checkbox label="改" name="log" disabled></el-checkbox>
              <el-checkbox label="查" name="log"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="管理员列表">
            <el-checkbox-group v-model="form.manager">
              <el-checkbox label="增" name="manager" disabled></el-checkbox>
              <el-checkbox label="删" name="manager" disabled></el-checkbox>
              <el-checkbox label="改" name="manager"></el-checkbox>
              <el-checkbox label="查" name="manager"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="角色管理">
            <el-checkbox-group v-model="form.role">
              <el-checkbox label="增" name="role"></el-checkbox>
              <el-checkbox label="删" name="role"></el-checkbox>
              <el-checkbox label="改" name="role"></el-checkbox>
              <el-checkbox label="查" name="role"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-divider content-position="left">部门权限</el-divider>
          <el-form-item label="部门">
            <el-checkbox-group v-model="form.depPower">
              <el-checkbox label="部门1" name="depPower"></el-checkbox>
              <el-checkbox label="部门2" name="depPower"></el-checkbox>
              <el-checkbox label="部门3" name="depPower"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <div style="text-align: left;">
            <el-button type="primary" @click="onSubmit">提交</el-button>
            <el-button>重置</el-button>
          </div>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      activeName: "first",
      gid: "",
      data: {},
      search:"",
      tableData: [
        {
          gid: "12345",
          name: "asdasd"
        },
        {
          gid: "36925",
          name: "sdfds"
        },
        {
          gid: "98563",
          name: "asfghd"
        },
        {
          gid: "25631",
          name: "ui"
        },
        {
          gid: "65412",
          name: "tryu"
        }
      ],
      form: {
        rid: "",
        name: "",
        person: [],
        log: [],
        manager: [],
        role: [],
        depPower: ""
      }
    };
  },
  created: function() {
    this.data = this.$route.params.data;
    this.form.rid = this.data.rid;
    this.form.name = this.data.name;
    let pagePower = parseInt(this.data.pagePower, 16).toString(2);
    console.log(this.data.pagePower,pagePower);
    if(pagePower);
    this.getList();
  },
  methods: {
    getList() {
      let here = this;
      axios({
        url: "/gllists",
        params: {
          rid: here.data.rid
        }
      })
        .then(function(response) {
          let data = response.data;
          here.tableData = data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    goBack() {
      this.$router.go(-1);
    },
    handleDelete(index, row) {
      this.$confirm("是否移除该管理员?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        let here = this;
        axios({
          url: "/gldel",
          params: {
            rid: here.data.rid,
            gid: row.gid
          }
        })
          .then(function(response) {
            if (response.data.success === 1) {
              this.$message({
                type: "success",
                message: "移除成功!"
              });
                this.getList();
            }else{
                this.$message.error("移除失败!");
            }
            
          })
          .catch(function(error) {
            console.log(error);
          });
      });
    },
    submit() {
      let here = this;
      axios({
        url: "/gladd",
        params: {
          gid: here.gid,
          rid: here.data.rid
        }
      })
        .then(function(response) {
          if (response.data.success === 1) {
            this.$message({
              message: "添加成功",
              type: "success"
            });
          } else {
            this.$message.error("添加失败!");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    onSubmit(){

    }
  }
};
</script>

<style lang="stylus" scoped>
.main {
  background: #ffffff;
  padding: 20px;
}

.select, .el-input {
  width: 156px;
  margin-bottom: 10px;
}

.select {
  display: inline;
}

.el-page-header {
  margin-bottom: 10px;
}

.el-divider__text {
  font-size: 12px;
  color: #606266;
}
</style>


